<template>
  <div id="app">
    <header class="header">POPYURARI</header>
    <main class="main">
      <div class="inner">
        <timer-clock
          :selected-time="selectedTime"
          @has-reset-time-event="hasResetTimeEventHandler"
          @time-left-event="timeLeftEventHandler"></timer-clock>
        <click-area 
          :is-reset-click="isResetClick"
          :is-click-active="isClickActive"
          @count-click-event="countClickEventHandler"
          @has-reset-click-event="hasResetClickEventHandler"></click-area>
        <timer-select-button 
          @reset-event="resetEventHandler"></timer-select-button>
      </div>
    </main>
    <footer class="footer">
      <the-footer />
    </footer>
  </div>
</template>

<script>
import {
  ref,
  computed
	} from 'vue'

import TimerClock from './components/TimerClock'
import TimerSelectButton from './components/TimerSelectButton'
import ClickArea from './components/ClickArea'
import TheFooter from './components/TheFooter'


export default {
  components: {
    TimerClock,
    TimerSelectButton,
    ClickArea,
    TheFooter
  },
  setup() {
    const selectedTime = ref(0);
    const timeLeft = ref(0);
    const isResetClick = ref(false);
    const numClicks = ref(0);

    const isClickActive = computed(() => {
			return timeLeft.value > 0;
		});

		const resetEventHandler = (centisec) => {
      selectedTime.value = centisec;
      isResetClick.value = true;
      return;
    };

    const timeLeftEventHandler = (centisec) => {
      timeLeft.value = centisec;
      return;
    };

    const hasResetTimeEventHandler = () => {
      selectedTime.value = 0;
      return;
    };

    const countClickEventHandler = () => {
      numClicks.value++;
      return;
    };

    const hasResetClickEventHandler = () => {
      isResetClick.value = false;
      numClicks.value = 0;
      return;
    };

		return {
      selectedTime,
      timeLeft,
      isResetClick,
      isClickActive,
      resetEventHandler,
      timeLeftEventHandler,
      hasResetTimeEventHandler,
      countClickEventHandler,
      hasResetClickEventHandler
		};
	}
}
</script>


