<template>
    <div class="clockStyle" :class="displayTimeLeftStyle">
        {{displayTimeLeft}}
    </div>  
</template>

<script>
import {
    ref,
    watch,
	computed
} from 'vue'

import {
    DiscriminateStates, 
    StateSet, 
    StateElement
} from '../assets/js/state-pattern.js'

const displayTimeLeftStyleFactory = (t) => {
    const displayTimeLeftStyle = new DiscriminateStates(
        new StateSet([
            new StateElement("full-time"), new StateElement("half-time"), new StateElement("empty-time")
        ]), 
        new StateSet([
            new StateElement(t/3*2), new StateElement(t/3), new StateElement(-1)
        ]),
        (concrete, criterion ) => {return concrete < criterion}
    )
    return displayTimeLeftStyle;
};

export default {
    props: {
        selectedTime: {
            type: Number,
            default: 0
		}
    },
    setup(props, {emit}) {
		var intervalTimer;
		const timeLeft = ref(0);
		const displayTimeLeftStyle = ref("full-time")

		const displayTimeLeft = computed(() => {
			const seconds = Math.floor(timeLeft.value/100);
			const centiseconds = timeLeft.value % 100;
			return `${zeroPadded(seconds)}:${zeroPadded(centiseconds)}`;
		});

        watch(() => props.selectedTime, (currentValue) => {
            if (currentValue!=0) {
                setTime(currentValue);
                emit("has-reset-time-event");
            }
            return;
		});

        const setTime = (centisecs) => {
            clearInterval(intervalTimer);
            timeLeft.value = centisecs;
            timer(centisecs);
            return;
        };

        const timer = (centisecs) => {
            const now = Date.now();
            const end = now + centisecs * 10;
            const displayStyle = displayTimeLeftStyleFactory(centisecs);
            countdown(end, displayStyle);
            return;
        };

        const countdown = (end, displayStyle) => {
            intervalTimer = setInterval(() => {
                var centisecsLeft = Math.round((end - Date.now()) / 10);
                timeLeft.value = centisecsLeft;
                emit("time-left-event", centisecsLeft)
                if(centisecsLeft < 0) {
                    clearInterval(intervalTimer);
                    timeLeft.value = 0;
                }
                displayTimeLeftStyle.value = displayStyle.show(centisecsLeft);
                return;
            }, 50);
            return;
        };

		return {
			timeLeft,
            displayTimeLeftStyle,
			displayTimeLeft,
            setTime,
            timer,
            countdown
		};
	}
}

function zeroPadded(num) {
    return num < 10 ? `0${num}` : num;
}
</script>

<style scoped>
* {
    padding: 0;
    margin: 0;
}

.clockStyle{
    padding-top: 3vh;
    font-size: 10vh;
}

.full-time{
    color:black;
}

.half-time{
    color:darkorange;
}

.empty-time{
    color:tomato;
}
</style>