<template>
    <div>
        <ul class="button-container">
            <li v-for="(time, index) of times" :key="index" >
                <a @click.prevent="setTime(time.centisec)" :class="[
                    'is-link'
                    ]">
                    {{ time.display }}
                </a>
            </li>
        </ul>
	</div>
</template>

<script>
import {
    ref} from 'vue'

export default {
  setup(props, {emit}) {		
		const times = ref([
			{
				centisec: 500,
				display: "5s"
			},
			{
				centisec: 1000,
				display: "10s"
			},
			{
				centisec: 1500,
				display: "15s"
			}
		]);

        const setTime = (centisec) => {
            emit("reset-event", centisec);
		};
		
		return {
			times,
			setTime
		};
	}
}
</script>

<style scoped>
* {
    padding: 0;
    margin: 0;
}

li{
    list-style: none;
}

.button-container {
    padding-top: 2vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.is-link {
    background-color: #33b5e5;
    border-color: transparent;

    font-size: 4vh;
    padding: 1.5vh 4vw;
    margin: 0 3vw;
    border-radius: 1.5vh;

}

.is-link:hover {
    background-color: #0099cc;
}
</style>