<template>
    <div>
        <p >
            <icon-base
                class="heart"
                width="6vh"
                height="6vh"
                viewBox="0 0 90 90"
                icon-name="icon-heart"
            >
                <icon-heart />
            </icon-base>
             x {{numClicks}}
        </p>
    </div>
    <image-side-bar>
        <div
            v-for="(itemImage, index) of itemImages" :key="index"
            v-show="showTheImage(itemImage.image)"
            @mousedown.prevent="mouseDownEventHandler"
            @mouseup.prevent="mouseUpEventHandler"
        >
            <icon-base 
                width="89vh"
                height="50vh"
                :viewBox="itemImage.viewBox"
                :icon-name="itemImage.image"
            >
                <component :is="itemImage.image"></component>
            </icon-base>
        </div>
    </image-side-bar>

</template>

<script>
import {
    ref,
    watch} from 'vue'

import ImageSideBar from './ImageSideBar.vue'

import IconBase from './IconBase.vue'
import IconHeart from './icons/IconHeart.vue'

//click images
import ImageCloseEye from './click-images/ImageCloseEye.vue'
import ImageOpenEye from './click-images/ImageOpenEye.vue'
import ImageBlush from './click-images/ImageBlush.vue'
import ImageXEye from './click-images/ImageXEye.vue'
import ImageMouthPouting from './click-images/ImageMouthPouting.vue'
import ImageTrembling from './click-images/ImageTrembling.vue'
import ImageShot from './click-images/ImageShot.vue'
import ImageTremblingRGB from './click-images/ImageTremblingRGB.vue'
import ImageShotRGB from './click-images/ImageShotRGB.vue'

import {
    DiscriminateStates, 
    StateSet, 
    StateElement
} from '../assets/js/state-pattern.js'

const mouseDownStyleStateSet = new StateSet([
    new StateElement("image-open-eye"),
    new StateElement("image-blush"),
    new StateElement("image-mouth-pouting"),
    new StateElement("image-shot-r-g-b"),
    new StateElement("image-shot")
]);
const mouseUpStyleStateSet = new StateSet([
    new StateElement("image-close-eye"),
    new StateElement("image-open-eye"),
    new StateElement("image-x-eye"),
    new StateElement("image-trembling-r-g-b"),
    new StateElement("image-trembling")
]);
const changeStyleCriteria = new StateSet([
    new StateElement(5),
    new StateElement(8),
    new StateElement(13),
    new StateElement(16),
    new StateElement(200)
]);

const mouseDownStyle = new DiscriminateStates(
    mouseDownStyleStateSet, 
    changeStyleCriteria,
    (concrete, criterion ) => {return concrete > criterion;}
);
const mouseUpStyle = new DiscriminateStates(
    mouseUpStyleStateSet, 
    changeStyleCriteria,
    (concrete, criterion ) => {return concrete > criterion;}
);

export default {
    props: {
        isResetClick: {
            type: Boolean,
            default: false
		},
        isClickActive: {
            type: Boolean,
            default: false
        }
    },
    setup(props, {emit}) {
        const numClicks = ref(0);
        const showedImage = ref("image-close-eye");

        //click images
        const itemImages = ref([
            {
                image: "image-close-eye",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-open-eye",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-blush",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-x-eye",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-mouth-pouting",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-trembling",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-shot",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-trembling-r-g-b",
                viewBox: "0 0 677.33333 381"
            },
            {
                image: "image-shot-r-g-b",
                viewBox: "0 0 677.33333 381"
            }
        ]);
        
        watch(() => props.isResetClick, (currentValue) => {
            if (currentValue==true) {
                numClicks.value = 0;
                mouseDownStyle.reset();
                mouseUpStyle.reset();
                showedImage.value = mouseUpStyle.show(0);
                emit("has-reset-click-event");
            }
		});

        const showTheImage = (name) => {
            return name == showedImage.value;
        };

        const mouseDownEventHandler = () => {
            if (props.isClickActive) {
                numClicks.value++;
                showedImage.value = mouseDownStyle.show(numClicks.value);
                emit("count-click-event");
                return;
            }
        };

        const mouseUpEventHandler = () => {
            if (props.isClickActive) {
                showedImage.value = mouseUpStyle.show(numClicks.value);
                return;
            }
        };

		return {
            numClicks,
            itemImages,
            showedImage,
            showTheImage,
            mouseDownEventHandler,
            mouseUpEventHandler
		};
	},
    components: {
        ImageSideBar,
        IconBase,
        IconHeart,
        ImageCloseEye,
        ImageOpenEye,
        ImageBlush,
        ImageXEye,
        ImageMouthPouting,
        ImageTrembling,
        ImageShot,
        ImageTremblingRGB,
        ImageShotRGB
    }
}


</script>

<style scoped>
* {
    padding: 0;
    margin: 0;
}

p{
    font-size: 6vh;
}
</style>