export class DiscriminateStates {
    constructor(statesSet, criteriaSet, discriminantFunction) {
        this.statesSet = statesSet;
        this.criteriaSet = criteriaSet;
        this.discriminantFunction = discriminantFunction;    
    }

    reset() {
        this.statesSet.reset();
        this.criteriaSet.reset();
        return;
    }

    show(concrete) {
        if (this.discriminantFunction(concrete, this.criteriaSet.show())) {
            this.statesSet.change();
            this.criteriaSet.change();
        }
        return this.statesSet.show();
    }
}

export class StateSet {
    constructor(statesSet) {
        this.statesSet = statesSet;
        this.currentState = this.statesSet[0];
    }

    change() {
        const totalStates = this.statesSet.length;
        let currentIndex = this.statesSet.findIndex(state => state === this.currentState);
        if (currentIndex + 1 < totalStates) {
            this.currentState = this.statesSet[currentIndex + 1];
        }
        else {
            this.currentState = this.statesSet[0];
        }
    }

    reset() {
        this.currentState = this.statesSet[0];
        return;
    }
    
    show() {
        return this.currentState.show();
    }
}

export class StateElement {
    constructor(stateElement) {
        this.stateElement = stateElement;
    }
    
    show() {
        return this.stateElement;
    }
}

